<template>
  <div class="data_all">
    <div class="data_all_title">
      <div class='data_all_title_name'>
        <span class="first-head-word">匠心</span><span class="scecond-head-word">配网</span>
      </div>
      <div class='data_all_title_search'>
        <!-- <van-search shape = "round" class="input-search-style" 
          v-model = 'searchContent'
          @focus = "enterSearch()"
          placeholder-style="color:#fff"
          placeholder="关键字、内容检索"/> -->
          <div class="search-div" @click="enterSearch()">
            <img src="../../assets/image/放大镜.png" alt />
            <span>关键词、内容检索</span>
          </div>
      </div>
      <div class='data_all_title_time' @click='enterStudyrank()'>
        <div class='data_all_time_top'>
          时长
        </div>
        <div class='data_all_time_bottom'>
          <div class="studyTime_top" v-if="parseInt(this.personWeekTime/1000/60/60)>=1">
              {{Number(parseInt(this.personWeekTime/1000/60/60))+Number((parseFloat(this.personWeekTime/1000 % 3600)/60/60).toFixed(1))}}h
            </div>
            <div class="studyTime_top" v-if="parseInt(this.personWeekTime/1000/60/60)<1">
              {{Math.ceil(this.personWeekTime/1000/60)}}m
            </div>
        </div>
      </div>
      <div class='data_all_shu'></div>
      <div class='data_all_title_rank' @click='enterStudyrank()'>
        <div class='data_all_title_rank_top'>排名</div>
        <div class='data_all_title_rank_bottom'>{{personPerTimeSort}}</div>
      </div>
    </div>
    <van-pull-refresh class="dataContainer" v-model="isLoading" @refresh="onRefresh()">
      <van-tabs
        v-show="!specialEmply"
        v-model="active"
        animated
        :ellipsis = false
        class="data_all_container"
        @change="changespecial"
      >
        <van-tab
          v-for="(itemBig,index) in classifyList"
          :title="itemBig.name"
          :key="itemBig.id"
          :name="itemBig.id"
        >
        <!-- 二级标题 -->
          <van-tabs
          @change="(name,title) =>{changeChildSpecial(name,title,itemBig)}"
          v-model="itemBig.isActiveChild"
          v-show="noChild"
          type='card'
          class='child_title'
          :ellipsis = false
          title-active-color='#fff'>
            <van-tab
              v-for="item in itemBig.children"
              :title="item.name"
              :name="item.id"
              :key="item.id"
            ></van-tab>
          </van-tabs>
          <!-- 内容 -->
          <div @touchstart='touchstartTabs(index)' @touchend='touchendTabs(index)'>
            <div class='middle'></div>
            <div class="data_container_bottom">
              <div class="journalism">
                <div class="journalism_top">
                  <div class="journalism_title">视听学习</div>
                  <div class="journalism_enter" @click.stop="enterVideoStudy()">
                    <img src="../../assets/image/back.png" alt />
                  </div>
                </div>
                <div class="journalism_container">
                  <div
                    v-for="item in journalismData"
                    :key="item.code"
                    class="journalism_item"
                    @click.stop="enterVideoDetail(item.id,item.specialCode)"
                  >
                    <div class="journalismImg">
                      <img
                        src="../../assets/image/videoImg.png"
                        alt
                        class="poster"
                        v-if="!item.imageUrl"
                      />
                      <img :src="item.imageUrl" alt class="poster" v-if="item.imageUrl" />
                      <div class="startPlay">
                        <img src="../../assets/image/播放.png" alt class="playButton" />
                      </div>
                    </div>
                    <div class="journalismName">{{item.name}}</div>
                  </div>
                </div>
                <div class="journalism_emply">
                  <img src="../../assets/image/emply.png" alt />
                  <div>空空如也~</div>
                </div>
              </div>
              <div class="wordStudy">
                <div class="wordStudy_top">
                  <div class="wordStudy_title">配网规范</div>
                  <div class="wordStudy_enter" @click="enterWord()">
                    <img src="../../assets/image/back.png" alt />
                  </div>
                </div>
                <div style="clear: both; font-size: 0.3rem; line-height: 0.3rem; display: flex;align-items: center;"><img  style="height:0.25rem;margin-right: 0.1rem;;"
                  src="../../assets/image/最新上传图标.png" alt />{{" "}}   <span>最新上传 </span></div>
                <div class="wordStudy_container" >
                  <div
                    v-for="item in wordStudyData"
                    :key="item.id"
                    class="wordStudy_item"
                    @click="enterStudy(item.id,item.name,item.specialCode,item.url,item.fileType)"
                  >
                    <div style='width:100%;height:100%;' >
                      <div class="wordStudycontainLeft">
                        <div class="wordStudyName">{{item.name}}</div>
                        <div class="wordStudyother">
                          <!-- <div class="wordStudychapter">共{{item.cataTotalNum}}章</div> -->
                          <div class="wordStudybrowse">浏览 {{item.viewNum}}</div>
                        </div>
                      </div>
                      <!-- <div class="wrap">
                        <div
                          :class="item.selfTrainEntity.schedule > 50 ? 'clip-auto circle' : 'circle'"
                        >
                          <div
                            v-if="(item.selfTrainEntity.schedule/100)=== 1"
                            class="percent left"
                            :style="`-webkit-transform:rotate(${360*(item.selfTrainEntity.schedule/100)}deg);clip: auto;`"
                          ></div>
                          <div
                            v-if="(item.selfTrainEntity.schedule/100)!== 1"
                            class="percent left"
                            :style="`-webkit-transform:rotate(${360*(item.selfTrainEntity.schedule/100)}deg);`"
                          ></div>
                          <div
                            :class="item.selfTrainEntity.schedule > 50 ? 'percent right' : 'percent right wth0'"
                          ></div>
                        </div>
                      </div> -->
                    </div>   
                  </div>
                </div>
                <div class="wordStudy_emply">
                  <img src="../../assets/image/emply.png" alt />
                  <div>空空如也~</div>
                </div>
              </div>
              <div class="wordStudy">
                <div class="wordStudy_top">
                    <div style="width:100%;font-size: 0.3rem; display: flex;align-items: center;">
                      <img  style="height:0.25rem;margin-right: 0.1rem;display:block" src="../../assets/image/最新上传图标.png" alt />
                      <span>阅读排行</span></div>
                </div>
                <div class="wordStudy_container" >
                  <div
                    v-for="item in readOrderData"
                    :key="item.id"
                    class="wordStudy_item"
                    @click="enterStudy(item.id,item.name,item.specialCode,item.url,item.fileType)"
                  >
                    <div style='width:100%;height:100%;' >
                      <div class="wordStudycontainLeft">
                        <div class="wordStudyName">{{item.name}}</div>
                        <div class="wordStudyother">
                          <!-- <div class="wordStudychapter">共{{item.cataTotalNum}}章</div> -->
                          <div class="wordStudybrowse">浏览 {{item.viewNum}}</div>
                        </div>
                      </div>
                      <!-- <div class="wrap">
                        <div
                          :class="item.selfTrainEntity.schedule > 50 ? 'clip-auto circle' : 'circle'"
                        >
                          <div
                            v-if="(item.selfTrainEntity.schedule/100)=== 1"
                            class="percent left"
                            :style="`-webkit-transform:rotate(${360*(item.selfTrainEntity.schedule/100)}deg);clip: auto;`"
                          ></div>
                          <div
                            v-if="(item.selfTrainEntity.schedule/100)!== 1"
                            class="percent left"
                            :style="`-webkit-transform:rotate(${360*(item.selfTrainEntity.schedule/100)}deg);`"
                          ></div>
                          <div
                            :class="item.selfTrainEntity.schedule > 50 ? 'percent right' : 'percent right wth0'"
                          ></div>
                        </div>
                      </div> -->
                    </div>  
                  </div>
                </div>
                <div class="wordStudy_emply">
                  <img src="../../assets/image/emply.png" alt />
                  <div>空空如也~</div>
                </div>
              </div>
            </div>
            <div class="container_emply">
              <img src="../../assets/image/container_emply.png" alt />
              <div>空空如也~</div>
            </div>
          </div>
        </van-tab>
      </van-tabs>
      <div class="special_emply" v-show="specialEmply">
        <img src="../../assets/image/container_emply.png" alt />
        <div>空空如也~</div>
      </div>
    </van-pull-refresh>
    <!-- 是否开始考试弹窗 -->
    <div class="startTextBox">
      <div class="startText">
        <img src="../../assets/image/模拟考试弹窗.png" alt />
        <div class="startText-mess">
          <div>
            时长
            <span style="font-size:0.46rem;color:#5794F6;">{{duration}}</span>分钟
          </div>
          <div>
            满分
            <span style="font-size:0.46rem;color:#5794F6;">{{fullScore}}</span>分
          </div>
        </div>
        <div class="startText-mess-two">
          <div class="startText-mess-two-wait" @click="exitBox">再等等</div>
          <div
            style="width:0.021rem;height:0.42rem;background:rgba(231,236,244,1);margin-top:0.3rem;"
          ></div>
          <div class="startText-mess-two-go" @click="startButton">立即考试</div>
        </div>
      </div>
    </div>
    <!-- 加载页面 -->
    <itemLoading :dialogVisible='isShowLoading'></itemLoading>
  </div>
</template>

<script>
import API from '@/api/api'
import $ from 'jquery'
import itemLoading from './../components/itemLoading'
import store from '@/store'

// import {mapGetters} from 'vuex'

export default {
  name: 'data_all',
  components: {
    itemLoading
  },
  inject: ['reload'],
  data () {
    return {
      isShowLoading: true, // 是否展示加载页
      fullScore: '', // 总分
      duration: '', // 考试时长
      active: '', // vant一级标题绑定name值
      classifyList: [], // 专项类型集合
      specialCode: '', // 专项类型
      specialCodeItem: {}, // 专项
      personPerTimeSort: '', // 个人排行
      personWeekTime: '', // 个人时长
      sxAllCount: 0, // 顺序练习的总题数
      collectCount: 0, // 收藏总题数
      errorCount: 0, // 错题集总题数
      sxLastNumber: 0, // 顺序练习上次的题号
      journalismData: [], // 视听中心数据
      wordStudyData: [], // 文档学习数据
      isLoading: false, // 下拉刷新
      selectMateBook: true, // 获取书籍数据
      selectMateVideo: true, // 获取视频数据
      selectReadOrder:true,  // 获取阅读排行数据
      getFindWeekTimeCG: true, // 获取个人信息
      tkGetExeCountCG: true, // 获取顺序练习题数
      tkGetErrorCountCG: true, // 获取顺序练习题数
      tkGetCollectCountCG: true, // 获取顺序练习题数
      findExeNumberCG: true, // 获取顺序练习上次看到的题号
      noChild: false, // 是否存在二级专项
      examId: '', // 模拟考试id
      touchStartX: 0, // 最初手指放下X轴的位置
      touchStartY: 0, // 最初手指放下Y轴的位置
      touchStartTime: 0, // 最初手指放下的时间
      searchContent:'',
      readOrderData:[],// 获取阅读排行数据
    }
  },
  beforeRouteLeave(to, from, next) {
    // 导航离开该组件的对应路由时调用
    // 可以访问组件实例 `this`
    if(to.name === 'data_list' || to.name === 'set'){
      store.dispatch('getAllspe').then(() => {
        localStorage.removeItem('activeItem') // 清除存储的处于高亮状态的
        localStorage.removeItem('classifyList') // 清除存储的专项使其重新刷新获取专项
        localStorage.removeItem("dataListSpecialCode")
      })
    }
    
    next();
  },
  created () {
    this.getFindWeekTime() // 获取个人信息
    this.getClassifyList() // 获取专项类型
  },
  mounted () {
    // console.log(...mapGetters);
    // 监听返回键
    this.$parent.isShow = 1 // 底部栏显示第而个高亮
    document.addEventListener('backbutton', this.onBackKeyDown, false) // 监听物理返回键
  },
  computed: {
    requestSuccess () {
      return (
        this.selectMateBook &&
        this.selectMateVideo &&
        this.getFindWeekTimeCG &&
        this.tkGetExeCountCG &&
        this.findExeNumberCG &&
        this.tkGetErrorCountCG &&
        this.tkGetCollectCountCG&&
        this.selectReadOrder
      )
    }
    //  ...mapGetters[
    //   'speList'
    // ]
  },
  // // 重新进入当前路由时清除缓存（目的为使其显示加载页）
  // beforeRouteEnter (to, from, next) {
  //   next(vm => { //  这里的vm指的就是vue实例，可以用来当做this使用
  //     localStorage.removeItem('isonfresh')
  //   })
  // },
  // 消除返回键监听
  beforeDestroy () {
    document.removeEventListener('backbutton', this.onBackKeyDown, false)
  },
  watch: {
    // 监听是否所有接口success而关闭加载页
    requestSuccess (val) {
      if (val === true) {
        this.isShowLoading = false
        setTimeout(() => {
          // this.$toast('刷新成功');
          this.isLoading = false
        }, 500)
      } else {
        setTimeout(() => {
          // this.$toast('刷新失败');
          this.isLoading = false
        }, 40000)
      }
    }
  },

  methods: {
    // 手指接触时触发事件
    touchstartTabs (index) {
      this.touchStartX = event.changedTouches[0].clientX
      this.touchStartY = event.changedTouches[0].clientY
      this.touchStartTime = event.timeStamp
      if ($('.van-pull-refresh__text').length > 0) {
        if ($('.van-pull-refresh__text')[0].innerHTML === '下拉即可刷新...') {
          $('.van-pull-refresh__track').css('transform', 'translate3d(0px, 0px, 0px)')
          // this.isLoading = false
        }
        if ($('.van-pull-refresh__text')[0].innerHTML === '释放即可刷新...') {
          // $('.van-pull-refresh__track').css('transform', 'translate3d(0px, 0px, 0px)')
          this.isLoading = true
          this.onRefresh()
        }
      }
    },
    // 手指离开时触发事件
    touchendTabs (index) {
      var offsetX = event.changedTouches[0].clientX - this.touchStartX// 根据手离开屏幕和触碰屏幕的正负判断
      var offsetY = event.changedTouches[0].clientY - this.touchStartY
      if ($('.van-pull-refresh__text').length > 0) {
        if ($('.van-pull-refresh__text')[0].innerHTML === '下拉即可刷新...') {
          console.log(1)
          $('.van-pull-refresh__track').css('transform', 'translate3d(0px, 0px, 0px)')
          // this.isLoading = false
        }
        if ($('.van-pull-refresh__text')[0].innerHTML === '释放即可刷新...') {
          // $('.van-pull-refresh__track').css('transform', 'translate3d(0px, 0px, 0px)')
          this.isLoading = true
          this.onRefresh()
        }
      }
      var time = event.timeStamp - this.touchStartTime// 根据在屏幕点击的时间判断是否翻页
      if (time < 1800 && offsetX < -20 && Math.abs(offsetY) <= Math.abs(offsetX)) {
        if (index < this.classifyList.length - 1) {
          console.log('next')
          this.active = this.classifyList[index + 1].id
        }
      } else if (time < 1800 && offsetX > 20 && Math.abs(offsetY) <= Math.abs(offsetX)) {
        if (index > 0) {
          console.log('prev')
          this.active = this.classifyList[index - 1].id
        }
      }
      event.stopPropagation()
    },
    // 上拉刷新
    onRefresh () {
      // store.dispatch('getAllspe').then(() => {
        // setTimeout(() => {
          this.getFindWeekTime() // 获取个人信息
          this.getClassifyList() // 获取专项类型
        // }, 700)
      // })
    },
    // tab的change
    changespecial (name, title) {
      this.specialCodeItem = this.classifyList.filter(i => i.id === name)[0] // 获取当前选中的对象
      if (!this.specialCodeItem.children) { // 判断当前选中对象是否存在二级专项
        this.specialCode = this.specialCodeItem.id
        this.noChild = false
      } else {
        this.noChild = true
        if (!this.specialCodeItem.isActiveChild) { // 判断当前选中对象是否存在之前选中的二级专项
          this.specialCode = this.specialCodeItem.children[0].id
        } else {
          this.specialCode = this.specialCodeItem.isActiveChild
        }
      }
      localStorage.setItem('activeItem', JSON.stringify(this.specialCodeItem)) // 存储当前一级专项的状态
      localStorage.setItem('dataListSpecialCode', this.specialCode)
      this.getSelectMateAndSche() // 获取该code下的文档和视频
      this.tkGetExeCount() // 获取顺序练习题数
      this.findExeNumber() // 获取顺序练习上次看到的题号
      this.getErrorNum() // 获取错题集的题数
      this.getCollectionNum() // 获取收藏题的题数
    },
    // 二级专项更改时
    changeChildSpecial (name, title, father) {
      father.isActiveChild = name // 使其一级专项增加属性保存最后点过的专项
      this.specialCode = name // 获取当前专项的code获取数据
      localStorage.setItem('classifyList', JSON.stringify(this.classifyList)) // 存储最新的专项列表
      this.specialCodeItem.isActiveChild = name
      localStorage.setItem('activeItem', JSON.stringify(this.specialCodeItem)) // 存储当前一级专项的状态
      localStorage.setItem('dataListSpecialCode', this.specialCode)
      this.getSelectMateAndSche() // 获取该code下的文档和视频
      this.tkGetExeCount() // 获取顺序练习题数
      this.findExeNumber() // 获取顺序练习上次看到的题号
      this.getErrorNum() // 获取错题集的题数
      this.getCollectionNum() // 获取收藏集的题数
    },
    // 返回键
    onBackKeyDown () {
      if ($('.data_all .startTextBox').css('display') === 'flex') { // 如果存在是否开始模拟考试弹窗时先关闭弹窗
        $('.data_all .startTextBox').css('display', 'none')
      } else { // 如果不存在是否开始模拟考试弹窗时直接退回桌面
        navigator.Backbutton.goHome()
      }
    },
    // 顺序练习的总题数
    tkGetExeCount () {
      this.tkGetExeCountCG = false
      API.post(
        'tkGetExeCount',
        {
          specialCode: this.specialCode
        },
        'json',
        (data, err) => {
          if (data.success) {
            this.sxAllCount = data.data
            this.tkGetExeCountCG = true
          } else {
            this.$toast(data.msg)
          }
        }
      )
    },
    // 错题集的总题数
    getErrorNum () {
      this.tkGetErrorCountCG = false
      API.post(
        'getErrorNum',
        {
          specialCode: this.specialCode
        },
        'json',
        (data, err) => {
          if (data.success) {
            this.errorCount = data.data
            this.tkGetErrorCountCG = true
          } else {
            this.$toast(data.msg)
          }
        }
      )
    },
    // 收藏集的总题数
    getCollectionNum () {
      this.tkGetCollectCountCG = false
      API.post(
        'collectionNum',
        {
          specialCode: this.specialCode
        },
        'json',
        (data, err) => {
          if (data.success) {
            this.collectCount = data.data
            this.tkGetCollectCountCG = true
          } else {
            this.$toast(data.msg)
          }
        }
      )
    },
    // 顺序练习上次看到的题号
    findExeNumber () {
      this.findExeNumberCG = false
      API.post(
        'findExeNumber',
        {
          specialType: this.specialCode
        },
        'json',
        (data, err) => {
          if (data.success) {
            this.sxLastNumber = data.data.exerciseNo
            this.findExeNumberCG = true
          } else {
            this.$toast(data.msg)
          }
        }
      )
    },
    // 确定模拟考试
    startButton () {
      this.$router.push({
        name: 'text_main_mes',
        params: { examId: this.examId, specialCode: this.specialCode }
      })
    },
    // 取消模拟考试弹窗
    exitBox () {
      $('.data_all .startTextBox').css('display', 'none')
    },
    // 点击模拟考试
    getmainMes () {
      API.post(
        'findexamDetail',
        {
          specialCode: this.specialCode
        },
        'json',
        (data, err) => {
          if (data.success) {
            if (data.data.length !== 0) {
              this.fullScore = data.data.fullScore
              this.duration = data.data.duration
              this.examId = data.data.examId
              $('.data_all .startTextBox').css('display', 'flex')
            } else {
              this.$toast('请设置出题机制')
            }
          } else {
            this.$toast(data.msg)
          }
        }
      )
    },
    // 点击进入不同的练习
    goStart (item) {
      // 顺序练习
      if (item === 1) { // 进入顺序练习
        this.$router.push({
          name: 'random_practice',
          params: {
            specialCode: this.specialCode,
            practiceStatus: item,
            numberMes: this.sxLastNumber
          }
        })
      } else if (item === 0) {
        // 错题集
        this.$router.push({ // 进入错题集
          name: 'error_practice',
          params: {
            specialCode: this.specialCode,
            practiceStatus: item,
            numberMes: 1
          }
        })
      } else if (item === 2) {
        // 随机练习
        this.$router.push({ // 进入随机练习
          name: 'random_practice',
          params: {
            specialCode: this.specialCode,
            practiceStatus: item,
            numberMes: 1
          }
        })
      } else if (item === 4) {
        // 收藏题库
        this.$router.push({ // 进入收藏题库
          name: 'collect_item',
          params: {
            specialCode: this.specialCode,
            practiceStatus: item,
            numberMes: 1
          }
        })
      } else if (item === 3) { // 点击模拟考试
        this.getmainMes()
      }
    },
    // 获取专题分类的接口
    getClassifyList () {
      if (localStorage.getItem('classifyList')) {
        this.classifyList = JSON.parse(localStorage.getItem('classifyList'))
      } else {
        this.classifyList = JSON.parse(JSON.stringify(this.$store.state.spe.speMap))
      }
      if (this.classifyList.length === 0) { // 判断是否存在专项
        this.specialEmply = true
        return
      } else {
        this.specialEmply = false
      }
      if (!localStorage.getItem('activeItem')) { // 判断是否存在上次所点的一级专项
        this.active = this.classifyList[0].id
        this.specialCodeItem = this.classifyList[0]
      } else {
        this.active = JSON.parse(localStorage.getItem('activeItem')).id
        this.specialCodeItem = JSON.parse(localStorage.getItem('activeItem'))
      }
      if (!this.specialCodeItem.children) { // 判断改专项是否存在二级专项
        this.specialCode = this.active
        this.noChild = false
      } else {
        this.noChild = true
        if (!this.specialCodeItem.isActiveChild) { // 判断该专项的二级专项是否存在之前的高亮状态
          this.specialCode = this.specialCodeItem.children[0].id
        } else {
          this.specialCode = this.specialCodeItem.isActiveChild
        }
      }
      localStorage.setItem('dataListSpecialCode', this.specialCode)
      this.getSelectMateAndSche() // 获取该code下的文档和视频
      this.tkGetExeCount() // 获取顺序练习题数
      this.findExeNumber() // 获取顺序练习上次看到的题号
      this.getErrorNum() // 获取错题集题数
      this.getCollectionNum() // 获取收藏集题数
    },
    // 获取个人信息
    getFindWeekTime () {
      this.getFindWeekTimeCG = false
      API.post('findWeekTime', {}, 'json', (data, err) => {
        if (data.success) {
          this.personPerTimeSort = data.data.one.perTimeSort
          this.personWeekTime = data.data.one.weekTime
          if (this.personWeekTime <= 1000 && this.personWeekTime > 0) {
            this.personWeekTime = 1000
          }
          this.getFindWeekTimeCG = true
        } else {
          this.$toast(data.msg)
        }
      })
    },
    // 获取内容
    getSelectMateAndSche () {
      this.selectMateVideo = false
      this.selectMateBook = false
      this.selectReadOrder = false
      // 获取此专项类型下的书籍
      API.post(
        'findNewMate',
        {
          specialCode: this.specialCode,
          // type: this.GLOBAL.TYPE.bookType,
          dayNum: 7 ,
          endCount: 10,
        },
        'json',
        (data, err) => {
          if (data.success) {
            this.wordStudyData = data.data
            this.ifEmply()
            this.selectMateBook = true
          } else {
            this.$toast(data.msg)
          }
        }
      )
      // 获取此专项类型下的视频
      API.post(
        'selectMateAndSche',
        {
          pageNum: this.GLOBAL.VIDEO.PAGE_NUM,
          pageSize: this.GLOBAL.VIDEO.PAGE_SIZE,
          specialCode: this.specialCode,
          type: this.GLOBAL.VIDEO.type
        },
        'json',
        (data, err) => {
          if (data.success) {
            // console.log(data)
            this.journalismData = data.data.records
            this.ifEmply()
            this.selectMateVideo = true
          } else {
            this.$toast(data.msg)
          }
        }
      )
      // 获取此专项类型下的阅读排行
      API.post(
        'findReadingRank',
        {
          specialCode: this.specialCode,
          endCount :10
        },
        'json',
        (data, err) => {
          if (data.success) {
            // console.log(data)
            this.readOrderData = data.data
            this.ifEmply()
            this.selectReadOrder = true   
          } else {
            this.$toast(data.msg)
          }
        }
      )
    },
    // 判断内容是否存在
    ifEmply () {
      if (this.wordStudyData.length === 0) {
        $('.wordStudy_enter').css('display', 'none')
      } else {
        $('.wordStudy_enter').css('display', 'flex')
      }
      if (this.journalismData.length === 0) {
        $('.journalism_enter').css('display', 'none')
      } else {
        $('.journalism_enter').css('display', 'flex')
      }
      if (this.wordStudyData.length === 0 && this.journalismData.length !== 0) {
        $('.wordStudy_emply').css('display', 'flex')
        $('.journalism_emply').css('display', 'none')
        $('.container_emply').css('display', 'none')
        $('.data_container_bottom').css('display', 'block')
      } else if (
        this.wordStudyData.length !== 0 &&
        this.journalismData.length === 0
      ) {
        $('.wordStudy_emply').css('display', 'none')
        $('.journalism_emply').css('display', 'flex')
        $('.container_emply').css('display', 'none')
        $('.data_container_bottom').css('display', 'block')
      } else if (
        this.wordStudyData.length === 0 &&
        this.journalismData.length === 0
      ) {
        $('.wordStudy_emply').css('display', 'none')
        $('.journalism_emply').css('display', 'none')
        $('.container_emply').css('display', 'flex')
        $('.data_container_bottom').css('display', 'none')
      } else {
        $('.wordStudy_emply').css('display', 'none')
        $('.journalism_emply').css('display', 'none')
        $('.container_emply').css('display', 'none')
        $('.data_container_bottom').css('display', 'block')
      }
    },
    // 进入视听学习中心
    enterVideoStudy () {
      localStorage.setItem('dataListSpecialCode', this.specialCode)
      this.$router.push({
        name: 'video_study'
      })
    },
    // 点击进入文档学习详情
    enterStudy (id, name, specialCode,url,fileType) {
      API.post(
        'selectLastContent',
        {
          trainMateId: id
        },
        'json',
        (data, err) => {
          if (data.success) {
            if (fileType === 'epub') {
              this.$router.push({
                name: 'text_train',
                params: { id: id, name: name, specialCode: specialCode }
              })
            } else {
              var u = navigator.userAgent // 判断是安卓还是ios
              var isAndroid = u.indexOf('Android') > -1 || u.indexOf('Linux') > -1 // g
              var isIOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/) // ios终端
              if (isAndroid) {
                cordova.InAppBrowser.open(url, '_system', 'footer=yes,closebuttoncaption="返回",hideurlbar=yes,hidenavigationbuttons=yes,location=no')
              }
              if (isIOS) {
                cordova.InAppBrowser.open(url, '_system', 'footer=yes,closebuttoncaption="返回",hideurlbar=yes,hidenavigationbuttons=yes,location=no')
              }
            }
          } else {
            this.$toast('该文档不存在')
            this.getSelectMateAndSche()
          }
        }
      )
    },
    // 进入文档学习
    enterWord () {
      localStorage.setItem('dataListSpecialCode', this.specialCode)
      this.$router.push({
        name: 'book_list'
      })
    },
    // 进入最近浏览
    rencently () {
      this.$router.push({
        name: 'recently_look'
      })
    },
    // 进入我喜欢
    mylike () {
      this.$router.push({
        name: 'me_like'
      })
    },
    // 进入搜索
    enterSearch () {
      localStorage.setItem('enterIndex', this.GLOBAL.ENTERSOURCE.studySearch)
      this.$router.push({
        name: 'search',
      })
    },
    // 进入学习排行榜
    enterStudyrank () {
      this.$router.push({
        name: 'study_rank_list'
      })
    },
    // 进入视频详情页
    enterVideoDetail (id, specialCode) {
      API.post(
        'findVideoDetilsById',
        {
          deflag: '0',
          id: id
        },
        'json',
        (data, err) => {
          if (data.success) {
            if (data.data) {
              this.$router.push({
                name: 'video_detail',
                params: { id, name, specialCode }
              })
            } else {
              this.$toast('该视频不存在')
              this.getSelectMateAndSche()
            }
          }
        }
      )
    }
  }
}
</script>
