  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.specialCode_select .specialCode_select_top > div img {
  width: 0.78rem;
  height: 0.79rem;
  display: block;
}
.specialCode_select .specialCode_select_top > div .select_word {
  top: 0.34rem;
  left: 0.34rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 0.25rem;
}
.specialCode_select .specialCode_select_top > div .select_word .select_type {
  font-size: 0.32rem;
  color: #444;
}
.specialCode_select
  .specialCode_select_top
  > div
  .select_word
  .select_textNumber {
  font-size: 0.22rem;
  color: #333;
  padding-top: 0.12rem;
  letter-spacing: 0.01rem;
}
.specialCode_select .specialCode_select_bottom {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.specialCode_select .specialCode_select_bottom > div {
  position: relative;
}
.specialCode_select .specialCode_select_bottom > div img {
  width: 3.3rem;
  height: 1.1rem;
  display: block;
}
.specialCode_select .specialCode_select_bottom > div .select_word {
  position: absolute;
  top: 0;
  left: 0;
  width: 3.3rem;
  height: 1.1rem;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 0.32rem;
  color: #fff;
}
.data_all .startTextBox {
  z-index: 2005;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.7);
  position: fixed;
  top: 0;
  left: 0;
  display: none;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.data_all .startText {
  width: 5.5rem;
  height: 3.81rem;
  position: relative;
  z-index: 999;
  display: flex;
  justify-content: center;
}
.data_all .startTextWord {
  position: absolute;
  z-index: 1000;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  /* justify-content: center; */
  flex-direction: column;
}
.data_all .practiceText {
  display: flex;
  justify-content: center;
  margin-top: 0.56rem;
  font-size: 0.36rem;
  color: #fff;
  text-shadow: 0px 3px 4px rgba(12, 101, 93, 0.36);
  letter-spacing: 0.04rem;
}
.data_all .practiceTextTime {
  margin-top: 1.06rem;
  display: flex;
  justify-content: center;
  width: 100%;
  height: 1.94rem;
  position: relative;
}
.data_all .practiceTextTime img {
  width: 1.96rem;
  height: 1.94rem;
}
.data_all .practiceTextTime .textTime {
  width: 100%;
  position: absolute;
  top: 0.65rem;
  left: 0;
}
