  font-size: 0.24rem;
  text-shadow: 0px 2px 4px rgba(35, 168, 183, 1);
  font-weight: bold;
  display: flex;
  justify-content: center;
  margin-left: -0.08rem;
}
.data_all .practiceTextTime .textTime .minuteTime {
  margin-top: 0.2rem;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  margin-left: -0.08rem;
}
.data_all .practiceTextTime .textTime .minuteTime b {
  color: #fff;
  font-size: 0.46rem;
  text-shadow: 0px 2px 4px rgba(35, 168, 183, 1);
  margin-right: 0.05rem;
}
.data_all .practiceTextTime .textTime .minuteTime span {
  font-weight: bold;
  color: #fff;
  font-size: 0.28rem;
  text-shadow: 0px 2px 4px rgba(35, 168, 183, 1);
  margin-bottom: 0.03rem;
}
.data_all .fullScore {
  display: flex;
  justify-content: center;
  margin-top: 0.19rem;
  color: #333;
  font-size: 0.24rem;
  align-items: center;
}
.data_all .fullScore span {
  font-size: 0.34rem;
  color: #04cd9d;
  margin: -0.02rem 0.05rem 0 0.05rem;
}
.data_all .studyTime_left_pm{
  color: rgba(255,255,255,0.7);
  font-size: 0.24rem;

}
.startTextBox .startButton {
  width: 100%;
  height: 0.74rem;
  position: absolute;
  bottom: 0.47rem;
  left: 0;
  text-align: center;
  z-index: 1000;
  display: flex;
  justify-content: center;
}
.startTextBox .startButton img {
  width: 4.26rem;
  height: 0.74rem;
  display: block;
}
.startTextBox .startButton span {
  /* display: flex; */
  width: 100%;
  /* height:0.74rem; */
  position: absolute;
  top: 0;
  left: 0;
  /* justify-content: center;
        align-items: center; */
  line-height: 0.78rem;
  color: rgba(255, 255, 255, 1);
  font-size: 0.3rem;
  z-index: 2;
  /* padding-top:0.035rem; */
}
.data_all .startText > img {
  width: 5.5rem;
  height: 3.81rem;
  z-index: 999;
}
.data_all .exitBox {
  margin-top: 0.5rem;
  width: 0.7rem;
  height: 0.7rem;
}
.data_all .exitBox img {
  width: 0.7rem;
  height: 0.7rem;
  display: block;
}
.startText-mess {
  width: 100%;
  position: absolute;
  z-index: 999;
  display: flex;
  justify-content: space-around;
  align-content: center;
  bottom: 1.35rem;
  left:0;
  color: #9fa7af;
  font-size: 0.3rem;
}
.startText-mess-two {
  width: 100%;
  height: 1rem;
  position: absolute;
  z-index: 999;
  display: flex;
  justify-content: space-around;
  align-content: center;
  bottom: 0;
  left:0;
  font-size: 0.34rem;
  border-top: 0.012rem solid #e7ecf4;
}
.startText-mess-two-wait {
  width: 48%;
  height: 100%;
  color: rgba(110, 116, 126, 1);
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 0.34rem;
}
.startText-mess-two-go {
  width: 48%;
  height: 100%;
  color: #5794F6;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 0.34rem;
}
.special_emply {
  width: 100%;
  /* height:3.23rem; */
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 1.5rem;
}
.special_emply img {
  width: 2.91rem;
  height: 2.63rem;
  display: block;
}
.special_emply div {
  color: #9fa7af;
  font-size: 0.32rem;
  margin-top: 0.4rem;
  margin-left: 0.02rem;
}
.data_all .middle{
  width:100%;
  height: 0.16rem;
  background: rgba(245, 246, 248, 1);
}
</style>
<style lang="stylus">
  .child_title{
    height:0.96rem;

  }
  .child_title .van-tabs__wrap{
    height:0.96rem !important;
    padding:0.23rem 0;
  }
  .child_title .van-tabs__nav--line{
    padding:0;
    height:0.5rem;
  }
  .child_title  .van-tabs__nav{
    align-items: center;
  }
  .data_all_container .van-tabs__line{
    background-color:#5794F6;
  }
  .van-tabs__content{
    background-color:#fff;
  }
  .data_all_container .van-tabs__nav{
    background:rgba(245, 246, 248, 1)
  }
  .data_all_container .child_title .van-tabs__nav{
    background-color:#fff;
  }
  .child_title .van-tabs__line{
    height:0.5rem;
    /* // width:1.8rem !important; */
    position: absolute;
    bottom: 0rem;
    left: 0;
    background-color:#5794F6;
    display:flex;
    align-items: center;
  }
  .child_title .van-ellipsis{
    position:relative;
    z-index:1000;
  }
  .data_all_container .van-tabs__nav .van-tab{
    padding:0 10px;
  }
  .child_title .van-tab{
    padding:0 5px !important;
  }
  .data_all_container .van-tab--active{
    color:#5794F6
  }
  .van-tabs__nav--card .van-tab.van-tab--active{
    background:#fff;
  }
  .data_all .van-tabs__nav--card .van-tab.van-tab--active span{
    max-width:100px
    background:#fff !important;
    color:#5794F6 !important;
  }
  .data_all .data_all_container .child_title .van-tabs__nav .van-tab span{
    max-width:200px;
    color:#898E8B;
    padding: 0 0.18rem !important;
    background: #fff;
    border-radius:6px;
    min-width:1.36rem
  }
  .data_all .data_all_container .child_title .van-tabs__nav .van-tab{
    background:#fff;
    border:0;
    display:flex;
    justify-content: center;
  }
  .data_all .van-tabs__nav--card{
    border:0
  }
  .data_all .van-tabs--card{
    padding-top:0;
    border:0 !important;
  }
  .mint-toast{
    line-height: 0.4rem !important;
    z-index: 9999 !important;
  }
  .data_all .van-field__control {
    display: block;
    box-sizing: border-box;
    width: 100%;
    min-width: 0;
    margin: 0;
    padding: 0;
    color: #fff;
    text-align: left;
    border: 0;
    font-size: 0.26rem;
    line-height: 0.26rem !important; 
}
.van-field__left-icon {
  margin-right: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #FFFFFF;

}
.van-search__content--round {
  border-radius: 17px;
  background-color: rgb(124,172,248);
  height: 0.54rem;
}
</style>



